<template>
  <div>
    <div v-show="showPage === 0">
  <el-container class="content">
    <el-header>
      <HeaderTitle class="header-title">
        <div slot="head-search" class="head-search">
          <el-form :inline="true" class="demo-form-inline" size="mini">
            <el-form-item label="姓名">
              <el-input v-model="searchName" placeholder="姓名"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="findList">{{
                BTN_TEXT.SEARCH
              }}</el-button>
              <el-button @click="onReset">{{ BTN_TEXT.RESET }}</el-button>
            </el-form-item>
          </el-form>
        </div>
        <div slot="head-operation" class="head-operation">
          <HeadOperationItem name="addDevice" @onOprationItem="addUser">
            <i slot="operation-icon" class="icon-add"></i>
            <span slot="operation-name"
              >{{ BTN_TEXT.ADD }}{{ COMPANY_USER.USER }}</span
            >
          </HeadOperationItem>
          <HeadOperationItem name="addDevice" @onOprationItem="exportData">
            <i slot="operation-icon" class="icon-export"></i>
            <span slot="operation-name">导出</span>
          </HeadOperationItem>
        </div>
      </HeaderTitle>
    </el-header>
    <el-main>
      <div class="area-main">
        <div class="area-aside">
          <HeaderTitle
            class="header-title"
            :titleName="COMPANY_USER.COMPANY_INFO"
          ></HeaderTitle>
          <ZTree :nodes="nodeData" @ztreeOnClick="ztreeOnClick"></ZTree>
        </div>
        <div class="area-info">
          <template>
            <!-- <div class="companyInformation">
              <companyInformation
                ref="companyInformation"
                :node="selectNodeData"
                @selectChange="selectChange"
              ></companyInformation>
            </div>-->
            <div class="userInformation">
              <HeaderTitle
                class="header-title"
                :titleName="COMPANY_USER.COMPANY_USER_INFO"
              >
                <div slot="head-search" class="head-search">
                  {{ ztreeSelectedNode.name }}
                </div>
              </HeaderTitle>
              <VXETable
                id="table"
                :height="$options.filters.getTableHeight(190)"
                :tableName="''"
                :tableLoading="tableLoading"
                :pager-config="tablePage"
                :tableDatas="tableDatas"
                :tableColumns="tableColumns"
                :tablePage="tablePage"
                @onTablePageChange="onTablePageChange"
                @chooseCheck="tableChooseCheck"
                ref="xGrid"
              >
                <div slot="tableOperation" class="tableOperations">
                  <div class="tableOperation-item">
                    <HeadOperationItem
                      name="batchDeleteStaff"
                      @onOprationItem="resetPwdAll()"
                    >
                      <i slot="operation-icon" class="icon-batchDelete"></i>
                      <span slot="operation-name">{{
                        BTN_TEXT.BATCH_RESET_PWD
                      }}</span>
                    </HeadOperationItem>
                    <HeadOperationItem
                      name="batchDeleteStaff"
                      @onOprationItem="deteleAll()"
                    >
                      <i slot="operation-icon" class="icon-batchDelete"></i>
                      <span slot="operation-name">{{
                        BTN_TEXT.BATCH_DELETE
                      }}</span>
                    </HeadOperationItem>
                  </div>
                </div>
              </VXETable>
            </div>
          </template>
        </div>
      </div>
    </el-main>
  </el-container>
    </div>

<div v-if="showPage === 1">
    <addUser @goBack="goBack"></addUser>
  </div>
  <el-dialog
    :class="[$store.state.dialogMarginClass]"
    :title="titleName"
    :visible.sync="dialogVisible"
  >
    <el-form :model="tempRow" :rules="rules" ref="editCodeForm">
      <el-row :gutter="22">
        <el-col :span="11">
          <el-form-item
            :label="CONSTANTS_USER.USERNAME"
            :label-width="formLabelWidth"
            prop="userName"
          >
            <el-input
              v-model="tempRow.userName"
              autocomplete="off"
              disabled
            ></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="11">
          <el-form-item
            :label="CONSTANTS_USER.WECHAT_OPEN_ID"
            :label-width="formLabelWidth"
            prop="wechatOpenId"
          >
            <el-input
              v-model="tempRow.wechatOpenId"
              autocomplete="off"
            ></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="11">
          <el-form-item
            :label="CONSTANTS_USER.REALNAME"
            :label-width="formLabelWidth"
            prop="realName"
          >
            <el-input v-model="tempRow.realName" autocomplete="off"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="11">
          <el-form-item
            :label="CONSTANTS_USER.TELEPHONE_NUMBER"
            :label-width="formLabelWidth"
            prop="telephoneNumber"
            :rules="filter_rules({ type: 'phone' })"
          >
            <el-input
              v-model="tempRow.telephoneNumber"
              @input="change"
              autocomplete="off"
            ></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="11">
          <el-form-item
            :label="CONSTANTS_USER.SEX"
            :label-width="formLabelWidth"
            prop="sex"
          >
            <el-select
              v-model="tempRow.sex"
              style="width: 100%"
              :placeholder="VARIABLE.PLEASE_CHOOSE"
            >
              <el-option
                v-for="item in sexOptions"
                :key="item.id"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="11">
          <el-form-item
            :label="CONSTANTS_USER.EMAIL"
            :label-width="formLabelWidth"
            prop="email"
          >
            <el-input v-model="tempRow.email" autocomplete="off"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="11">
          <el-form-item
            :label="CONSTANTS_USER.OPERATOR_JOR_NUMBER"
            :label-width="formLabelWidth"
            prop="operatorJorNumber"
          >
            <el-input
              v-model="tempRow.operatorJorNumber"
              autocomplete="off"
            ></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="11">
          <el-form-item
            :label="CONSTANTS_USER.WECHAT_NICK_NAME"
            :label-width="formLabelWidth"
            prop="wechatNickName"
          >
            <el-input
              v-model="tempRow.wechatNickName"
              autocomplete="off"
            ></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="11">
          <el-form-item
            :label="CONSTANTS_USER.PWD"
            :label-width="formLabelWidth"
          >
            <el-input
              v-model="tempRow.newPassword"
              autocomplete="off"
              show-password
            ></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="11">
          <el-form-item
            :label="CONSTANTS_USER.ROLE"
            :label-width="formLabelWidth"
          >
            <el-select
              multiple
              v-model="tempRow.roleIdList"
              style="width: 100%"
              :placeholder="VARIABLE.PLEASE_CHOOSE"
            >
              <el-option
                v-for="item in roleOptions"
                :key="item.roleId"
                :label="item.roleName"
                :value="item.roleId"
              ></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="11">
          <el-form-item
            :label="CONSTANTS_USER.ISPWD"
            :label-width="formLabelWidth"
            :rules="newPasswordRules"
            prop="isNewPassword"
          >
            <el-input
              v-model="tempRow.isNewPassword"
              autocomplete="off"
              show-password
            ></el-input>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
    <span slot="footer" class="dialog-footer">
      <el-button type="primary" @click="submitEdit()">{{
        BTN_TEXT.SAVE
      }}</el-button>
      <el-button type="warning" @click="resetEdit">{{
        BTN_TEXT.RESET
      }}</el-button>
      <el-button @click="cancelBtnClick">{{ BTN_TEXT.CANCEL }}</el-button>
    </span>
  </el-dialog>
    </div>
</template>

<script>
import js from '@/assets/js/company/CompanyUsers/index';
export default js;
</script>

<style lang="less" scoped>
@import '../../../assets/less/company/CompanyUsers/index.less';
</style>
