// 引入标题组件
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// 引入标题元素组件
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
// 引入树形组件
import ZTree from '@/components/common/ZTree/ZTree';
// 引入公司信息组件
import companyInformation from '@/views/company/CompanyUsers/companyInformation.vue';
// 引入类型信息组件
import addUser from '@/views/company/CompanyUsers/addUser.vue';
import { STATUS_CODE, TITLE, VARIABLE, BTN_TEXT, COMPANY_USER, CONSTANTS_USER, MSG } from '@/utils/constantsFile';
import VXETable from '@/components/common/Table';
import { showSuccessMessage, showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
// 导入所需接口
import {
  companyGetCompanyListAction,
  userUpdateByIdAction,
  roleGetAllAction,
  // companyUpdateByIdAction,
  userGetListAction
} from '@/api/management';
export default {
  name: 'CompanyUsers',
  components: {
    // 加载树形组件
    ZTree,
    // 加载公司信息组件
    companyInformation,
    VXETable,
    HeaderTitle,
    HeadOperationItem,
    addUser
  },
  data() {
    var setValidateForm = (rule, value, callback) => {
      return this.$options.filters.formatterFormValidate(
        rule,
        value,
        callback,
        this.extendPropertyValueVoList
      );
    };
    var checkNewPassword = (rule, value, callback) => {
      if (this.tempRow.newPassword !== '' && this.tempRow.newPassword !== value) {
        callback(new Error('两次密码不一致'));
      } else {
        callback();
      }
    };

    return {
      VARIABLE: VARIABLE,
      CONSTANTS_USER: CONSTANTS_USER,
      setValidateForm: setValidateForm,
      BTN_TEXT: BTN_TEXT,
      COMPANY_USER: COMPANY_USER,
      // 标题
      titleName: CONSTANTS_USER.TITLE_MODIFY_USER_DIALOG,
      // 修改对话框是否可见，默认为false，表示不可见
      dialogVisible: false,
      formLabelWidth: '100px',
      roleOptions: [],
      // 判断显示组件
      showPage: 0,
      searchName: '',
      // ztree对象
      ztreeObj: {},
      // ztree选中数据
      ztreeSelectedNode: {},
      // ztree数据
      nodeData: [],
      // 暂存修改后的row数据
      tempRow: {},
      // 表单数据
      form: {},
      // 选中节点数据
      selectNodeData: '',
      // table加载动画
      tableLoading: false,
      // 表单校验规则
      newPasswordRules: [
        { validator: checkNewPassword, trigger: 'blur' }
      ],
      // 性别下拉
      sexOptions: [
        {
          label: VARIABLE.MAN,
          value: VARIABLE.ONE_NUMBER
        },
        {
          label: VARIABLE.WOMAN,
          value: VARIABLE.TWO_NUMBER
        }
      ],
      rules: {
        userName: [
          {
            required: true,
            trigger: 'blur',
            message: MSG.PLEASE_ENTER_CONTENT
          },
          { max: 20, message: '长度不得大于20个字符' }
        ],
        realName: [
          {
            required: true,
            trigger: 'blur',
            message: MSG.PLEASE_ENTER_CONTENT
          },
          { max: 10, message: '长度不得大于10个字符' },
          { pattern: /^[A-Za-z0-9\u4e00-\u9fa5]+$/, message: '不允许输入空格等特殊符号' }
        ],
        operatorJorNumber: [
          {
            required: true,
            trigger: 'blur',
            message: MSG.PLEASE_ENTER_CONTENT
          },
          { max: 30, message: '长度不得大于30个字符' }
        ],
        sex: [
          {
            required: true,
            trigger: 'change',
            message: MSG.PLEASE_CHOOSE_CONTENT
          }
        ],
        email: [
          {
            type: 'email',
            message: CONSTANTS_USER.RULES_EMAIL,
            trigger: ['blur']
          },
          { max: 20, message: '长度不得大于20个字符' }
        ],
        userCrad: [
          {
            validator: this.isIdCardValid,
            trigger: 'change'
          },
          { max: 20, message: '长度不得大于20个字符' }
        ],
        newPassword: [
          { max: 12, message: '长度不得大于12个字符' }
        ],
        wechatNickName: [
          { max: 30, message: '长度不得大于30个字符' }
        ],
        wechatOpenId: [
          { max: 100, message: '长度不得大于100个字符' }
        ]
      },
      // table分页
      tablePage: {
        total: 0,
        currentPage: 1,
        pageSize: 5,
        align: 'right',
        pageSizes: [5, 20, 50, 100, 200, 500],
        layouts: [
          'Sizes',
          'PrevJump',
          'PrevPage',
          'Number',
          'NextPage',
          'NextJump',
          'FullJump',
          'Total'
        ],
        perfect: true,
        slots: {
          // 使用自定义插槽
          left: 'pager_left'
        }
      },
      // table数据
      tableDatas: [],
      // table表头配置
      tableColumns: [
        {
          type: 'checkbox',
          width: '50',
          fixed: 'left',
          align: 'center'
        },
        {
          field: 'userName',
          title: COMPANY_USER.USERNAME,
          align: 'center'
        },
        {
          field: 'realName',
          title: COMPANY_USER.REALNAME,
          align: 'center'
        },
        {
          field: 'telephoneNumber',
          title: COMPANY_USER.TELEPHONE_NUMBER,
          align: 'center'
        },
        {
          field: 'email',
          title: COMPANY_USER.EMAIL,
          align: 'center'
        },
        {
          field: 'role',
          title: COMPANY_USER.ROLE,
          align: 'center'
        },
        {
          field: '',
          title: BTN_TEXT.OPERATION,
          width: '300',
          fixed: 'right',
          align: 'center',
          slots: {
            default: ({ row, column }) => {
              return [
                <el-button onClick={() => this.toUpdate(row)} type="text">
                  {BTN_TEXT.MODIFY}
                </el-button>,
                <el-button onClick={() => this.toResetPwd(row)} type="text">
                  {COMPANY_USER.RESET_PWD}
                </el-button>,
                <el-button onClick={() => this.setDelete(row)} type="text">
                  {BTN_TEXT.DELETE}
                </el-button>
              ];
            }
          }
        }
      ]
    };
  },
  computed: {},
  created() {
    // 调用查询接口
    this.getTree();
  },
  methods: {
    addUser() { },
    onReset() {
      this.tablePage.currentPage = 1;
      this.searchName = '';
      // 查询列表数据
      this.findList();
    },
    tableChooseCheck() { },
    deteleall() { },
    resetPwdAll() { },
    toUpdate(row) {
      // 执行弹出模态框时扩展字段和验证处理
      this.toSearchRole();
      this.dialogVisible = true;
      // 保存row修改过的数据
      this.tempRow = JSON.parse(JSON.stringify(row));

      // 保存row的初始数据，用于重置
      this.initRow = row;
    },
    toResetPwd() { },
    setDelete() { },
    async toSearchRole() {
      try {
        const res = await roleGetAllAction();
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.roleOptions = res.rows;
        } else {
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(this.GLOBAL.TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 树节点点击事件
     * @param event 事件对象
     * @param nodes 点击数据
     * @param obj ztree对象
     * @return: 无
     * @author: ZJM
     */
    ztreeOnClick(event, nodes, obj) {
      // 获取ztree的对象值
      this.nodeObj = obj;
      // 获取点击节点数据
      this.ztreeSelectedNode = nodes;
      // 调用子组件查询公司数据
      // this.$refs.companyInformation.getData(nodes.id);
      // 重置页数
      this.tablePage.currentPage = 1;
      // 查询列表数据
      this.findList();
    },
    /**
     * @description: 查询类型树形数据
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    async getTree() {
      try {
        const res = await companyGetCompanyListAction({}, {
          conditionJson1: [{
            column: 'state', // 参数名
            operator: 1, // 并且
            ptype: 'string', // 参数类型
            type: 'in', // 包含
            value: '1,3' // 参数值
          }]
        });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 执行map方法将数据转换为新的数组
          const _arr = res.rows.map(item => {
            const json = {};
            json.id = item.companyId;
            json.pId = 0;
            json.state = item.state;
            json.isParent = false;
            // json.yName = item.companyName;
            // 若状态为3【停机】
            // if (json.state === 3) {
            //   // 显示公司名称 + 停机状态
            //   json.name = item.companyName + '(停用)';
            // } else {
            // 显示公司名称
            json.name = item.companyName;
            // }
            return json;
          });
          // 将新数据写入树形
          this.nodeData = _arr;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    // /**
    //  * @description: 公司启用和停用状态切换
    //  * @param val 状态
    //  * @return: 无
    //  * @author: ZJM
    //  */
    // async selectChange(val) {
    //   try {
    //     const res = await companyUpdateByIdAction({
    //       companyId: this.ztreeSelectedNode.id,
    //       state: val
    //     });
    //     if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
    //       // 若状态为1【启用】
    //       if (val === 1) {
    //         // 修改公司名为原始公司名
    //         this.ztreeSelectedNode.name = this.ztreeSelectedNode.yName;
    //       } else if (val === 3) {
    //         // 若状态为3【停机】
    //         // 修改公司名家属停用状态
    //         // this.ztreeSelectedNode.name =
    //         // this.ztreeSelectedNode.yName + '(停用)';
    //         this.ztreeSelectedNode.name = this.ztreeSelectedNode.yName;
    //       }
    //       // 修改对应树节点信息
    //       this.nodeObj.updateNode(this.ztreeSelectedNode);
    //       showSuccessMessage(res.message);
    //     } else {
    //       // 提示异常信息
    //       showWarningMessage(res.message);
    //     }
    //   } catch (error) {
    //     showErrorNotify(TITLE.NOTIFY, error);
    //   } finally {
    //   }
    // },
    /**
     * @description: table分页
     * @param currentPage 第几页
     * @param pageSize 一页几条
     * @return: 无
     * @author: ZJM
     */
    onTablePageChange(currentPage, pageSize) {
      // 点击页数写入table分页配置
      this.tablePage.currentPage = currentPage;
      // 一页条数夜路table分页配置
      this.tablePage.pageSize = pageSize;
      // 调用查询接口
      this.findList();
    },
    /**
     * @description: 切换主页面
     * @param val 状态
     * @return: 无
     * @author: ZJM
     */
    goBack(val) {
      this.showPage = val;
      this.findList();
    },
    /**
     * @description: 查询所属用户
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    async findList() {
      if (!this.ztreeSelectedNode.id) {
        return false;
      }
      try {
        this.tableLoading = true;
        const params = {
          current: this.tablePage.currentPage,
          size: this.tablePage.pageSize
        };
        // params.companyId = this.ztreeSelectedNode.id;
        // if (this.searchName !== '') {
        //   params.realName = this.searchName;
        // }
        const res = await userGetListAction(params,
          {
            conditionJson1: [
              {
                column: 'companyId',
                operator: 1,
                ptype: 'int',
                type: 'eq',
                value: this.ztreeSelectedNode.id
              },
              {
                column: 'realName',
                operator: 1,
                ptype: 'string',
                type: 'like',
                value: this.searchName
              }
            ]
          });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.tableDatas = res.rows;
          this.tablePage.total = res.total;
          this.tableLoading = false;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 导出所属用户
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    async exportData() {
      if (!this.ztreeSelectedNode.id) {
        return false;
      }
      try {
        this.tableLoading = true;
        const params = {
          current: VARIABLE.ONE_NUMBER,
          size: VARIABLE.BIG_NUMBER
        };
        const res = await userGetListAction(params,
          [
            {
              column: 'companyId',
              operator: 1,
              ptype: 'int',
              type: 'eq',
              value: this.ztreeSelectedNode.id
            },
            {
              column: 'realName',
              operator: 1,
              ptype: 'string',
              type: 'like',
              value: this.searchName
            }
          ]);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.tableLoading = false;
          this.$refs.xGrid.exportData(COMPANY_USER.COMPANY_USER_INFO, res.rows);
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 提交编辑后的行数据
     * @param {null} 无
     * @return: null
     */
    submitEdit() {
      this.$refs.editCodeForm.validate((valid) => {
        if (valid) {
          this.setSubmitEdit();
        }
      });
    },
    async setSubmitEdit() {
      try {
        const form = JSON.parse(JSON.stringify(this.tempRow));
        delete form.createTime;
        delete form.updateTime;
        delete form.extendPropertyValueVoList;
        delete form.companyId;
        delete form._XID;
        delete form.isNewPassword;
        if (form.newPassword !== '') {
          form.password = form.newPassword;
        }
        // 获取响应结果
        const res = await userUpdateByIdAction(form);

        // 响应为true，表示修改提交成功
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 弹出消息提示
          showSuccessMessage(res.message);
          // 隐藏对话框
          this.dialogVisible = false;
          this.initRow = null;
          // this.setUserReleRole();
          // 调用表格初始化方法，获取修改后的数据
          setTimeout(() => {
            if (this.extendPropertyValueVoList.length > 0) {
              this.saveExtendValue();
            } else {
              this.findTableList();
            }
          }, 0);
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        this.$notify.error({
          title: TITLE.ERROR,
          message: error,
          showClose: false,
          duration: 2000,
          position: 'bottom-right'
        });
      }
    },
    /**
     * @description: 重置编辑，恢复最初的数据
     * @param {null} null
     * @return: null
     */
    resetEdit() {
      this.tempRow = { ...this.initRow };
      this.showDialog(this.initRow);
      this.$refs.editCodeForm.resetFields();
    },
    /**
     * @description: （修改编码对话框）“取消”按钮点击事件
     * @param 无
     * @return: void
     */
    cancelBtnClick() {
      this.$refs.editCodeForm.resetFields();
      this.dialogVisible = false;
    },
    change() {
      this.$forceUpdate();
    }
  }
};
