/*
 * @Descripttion: 新增用户js
 * @version: 0.1
 * @Author: ZJM
 * @Date: 2020-06-17 09:41:01
 * @LastEditors: ZJM
 * @LastEditTime: 2021-03-02 17:32:49
 */
// 引入标题组件
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// 引入标题元素组件
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
// 导入所需接口
import {
  userInsertBatchAction,
  userCheckUserNameRepeatAction,
  roleGetAllAction,
  userRoleRelationshipInsertOrUpdateBatchAction
} from '@/api/management';
import {
  extendPropertyGetListAction,
  extendPropertyValueInsertOrUpdateBatchAction
} from '@/api/extendConfig';
import { showSuccessMessage, showWarningMessage } from '@/utils/message';
import { TITLE, STATUS_CODE, MSG, VARIABLE, BTN_TEXT, CONSTANTS_USER, FORM_CONFIG_DATE } from '@/utils/constantsFile';
import { showErrorNotify } from '@/utils/notify';
export default {
  components: {
    // 加载标题组件
    HeaderTitle,
    HeadOperationItem
  },
  props: {
  },
  data() {
    // 扩展字段通用验证
    const setValidateForm = ({ column, row, cellValue }) => {
      return new Promise((resolve, reject) => {
        return this.$options.filters.formatterTableValidate(column, cellValue, this.extendPropertyValueVoList, resolve, reject);
      });
    };

    // 验证用户名
    const userNameValid = ({ cellValue }) => {
      return new Promise((resolve, reject) => {
        userCheckUserNameRepeatAction({
          userName: cellValue
        })
          .then(res => {
            // 请求数据正常
            if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
              if (res.rows) {
                resolve();
              } else {
                reject(new Error(CONSTANTS_USER.RULES_USER_NAME));
              }
            }
          });
      });
    };

    // 验证邮箱
    const emailValid = ({ cellValue }) => {
      return new Promise((resolve, reject) => {
        var filter = /^([A-Za-z0-9_+-.])+@([A-Za-z0-9\-.])+\.([A-Za-z]{2,22})$/;
        if (cellValue) {
          if (filter.test(cellValue)) {
            resolve();
          } else {
            reject(new Error(CONSTANTS_USER.RULES_EMAIL));
          }
        } else {
          resolve();
        }
      });
    };

    // 验证电话
    const phoneValid = ({ cellValue }) => {
      return new Promise((resolve, reject) => {
        var filter = /(^(\d{3,4}-)?\d{7,8})$|(^1[34578]\d{9})$/;
        if (cellValue) {
          if (filter.test(cellValue)) {
            resolve();
          } else {
            reject(new Error(CONSTANTS_USER.RULES_PHONE));
          }
        } else {
          resolve();
        }
      });
    };

    return {
      setValidateForm: setValidateForm,
      BTN_TEXT: BTN_TEXT,
      MSG: MSG,
      imageUrl: '',
      // 页面标题
      titleName: CONSTANTS_USER.ADD_USER,
      // 表单校验规则
      validRules: {
      },
      // 原始数据
      OldRules: {
        userName: [
          { validator: userNameValid },
          {
            required: true,
            message: MSG.PLEASE_ENTER_CONTENT
          },
          { max: 20, message: '长度不得大于20个字符' }
        ],
        realName: [
          {
            required: true,
            message: MSG.PLEASE_ENTER_CONTENT
          },
          { max: 10, message: '长度不得大于10个字符' },
          { pattern: /^[A-Za-z0-9\u4e00-\u9fa5]+$/, message: '不允许输入空格等特殊符号' }
        ],
        operatorJorNumber: [
          {
            required: true,
            message: MSG.PLEASE_ENTER_CONTENT
          },
          { max: 30, message: '长度不得大于30个字符' }
        ],
        sex: [
          {
            required: true,
            message: MSG.PLEASE_CHOOSE_CONTENT
          }
        ],
        password: [
          {
            required: true,
            message: MSG.PLEASE_ENTER_CONTENT
          },
          { max: 12, message: '长度不得大于12个字符' }
        ],
        wechatNickName: [
          { max: 30, message: '长度不得大于30个字符' }
        ],
        wechatOpenId: [
          { max: 100, message: '长度不得大于100个字符' }
        ],
        email: [
          { validator: emailValid },
          { max: 20, message: '长度不得大于20个字符' }
        ],
        telephoneNumber: [
          { validator: phoneValid },
          { max: 11, message: '长度不得大于11个字符' }
        ]
      },
      // table数据
      tableDatas: [],
      // table表头配置
      tableColumns: [],
      // 原始数据
      tableColumns_old: [
        {
          field: 'userName',
          title: CONSTANTS_USER.USERNAME,
          minWidth: '150',
          align: 'center',
          editRender: { name: 'input' }
        },
        {
          field: 'operatorJorNumber',
          title: CONSTANTS_USER.OPERATOR_JOR_NUMBER,
          minWidth: '140',
          align: 'center',
          editRender: { name: 'input' }
        },
        {
          field: 'wechatNickName',
          title: CONSTANTS_USER.WECHAT_NICK_NAME,
          minWidth: '140',
          align: 'center',
          editRender: { name: 'input' }
        },
        {
          field: 'wechatOpenId',
          title: CONSTANTS_USER.WECHAT_OPEN_ID,
          minWidth: '140',
          align: 'center',
          editRender: { name: 'input' }
        },
        {
          field: 'realName',
          title: CONSTANTS_USER.REALNAME,
          minWidth: '140',
          align: 'center',
          editRender: { name: 'input' }
        },
        {
          field: 'sex',
          title: CONSTANTS_USER.SEX,
          minWidth: '140',
          align: 'center',
          editRender: {
            name: '$select',
            options: [
              {
                label: VARIABLE.MAN,
                value: VARIABLE.ONE_NUMBER
              },
              {
                label: VARIABLE.WOMAN,
                value: VARIABLE.TWO_NUMBER
              }
            ]
          }
        },
        {
          field: 'telephoneNumber',
          title: CONSTANTS_USER.TELEPHONE_NUMBER,
          minWidth: '140',
          align: 'center',
          editRender: { name: 'input' }
        },
        {
          field: 'email',
          title: CONSTANTS_USER.EMAIL,
          minWidth: '140',
          align: 'center',
          editRender: { name: 'input' }
        },
        {
          field: 'role',
          title: CONSTANTS_USER.ROLE,
          minWidth: '140',
          align: 'center',
          editRender: {
            name: 'ElSelect',
            options: [],
            props: { multiple: true }
          }
        },
        {
          field: 'password',
          title: CONSTANTS_USER.PWD,
          minWidth: '140',
          align: 'center',
          editRender: { name: 'input' }
        },
        {
          field: '',
          title: BTN_TEXT.OPERATION,
          width: '100',
          fixed: 'right',
          align: 'center',
          slots: {
            default: ({ row, column }) => {
              return [
                <i
                  onClick={() => this.copyRow(row, column)}
                  class="icon-table-addRow"
                ></i>,
                <i
                  onClick={() => this.deleteRow(row, column)}
                  class="icon-table-delete"
                ></i>
              ];
            }
          }
        }
      ],
      // 表头前部分
      tableColumns_head: [],
      // 表头操作部分
      tableColumns_operation: [],
      // 扩展字段数据集
      extendPropertyValueVoList: []
    };
  },
  computed: {},
  created() {
    this.initExtendTable();
  },
  mounted() {
  },
  methods: {
    /**
     * @description: 查询页面的扩展字段
     * @return: 无
     * @author: ZJM
     */
    async initExtendTable() {
      try {
        const res = await extendPropertyGetListAction(
          {},
          {
            conditionJson1: [
              {
                column: 'extendType',
                conditionJson1: [],
                conditionJson2: [],
                current: 0,
                operator: 1,
                operatorGroup: 0,
                orderBy: {},
                ptype: 'int',
                size: 0,
                type: 'eq',
                value: 4
              }
            ]
          });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.extendPropertyValueVoList = res.rows;
          this.setinitExtend(res.rows);
          this.extendRules(res.rows);
          // 默认新增一行
          this.insertEvent();
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 生成表头扩展字段
     * @return: 无
     * @author: ZJM
     */
    setinitExtend(row) {
      this.tableColumns = [];
      this.tableColumns_head = this.tableColumns_old.slice(0, this.tableColumns_old.length - 1);
      this.tableColumns_operation = this.tableColumns_old[this.tableColumns_old.length - 1];
      this.tableColumns = this.tableColumns.concat(this.tableColumns_head);
      if (row.length > 0) {
        for (var i = 0; i < row.length; i++) {
          if (row[i].propertyType === 1) {
            this.tableColumns.push({
              field: row[i].propertyCode,
              title: row[i].extendName,
              width: '160',
              align: 'center',
              className: i,
              editRender: { name: 'input' }
            });
          } else if (row[i].propertyType === 2) {
            this.tableColumns.push({
              field: row[i].propertyCode,
              title: row[i].extendName,
              width: '160',
              align: 'center',
              className: i,
              editRender: { name: '$input', props: { type: 'number' } }
            });
          } else if (row[i].propertyType === 3) {
            var num = '0';
            if (row[i].propertyLength === 1) {
              num = '220';
            } else if (row[i].propertyLength === 2) {
              num = '160';
            } else if (row[i].propertyLength === 3) {
              num = '160';
            }
            this.tableColumns.push({
              field: row[i].propertyCode,
              title: row[i].extendName,
              width: num,
              align: 'center',
              className: i,
              editRender: { name: 'ElDatePicker', props: { type: 'datetime', format: FORM_CONFIG_DATE[row[i].propertyLength] } }
            });
          }
        }
      }
      this.tableColumns = this.tableColumns.concat(this.tableColumns_operation);
      this.toSearchRole();
    },
    /**
     * @description: 移除一行
     * @param row 行数据
     * @return: 无
     * @author: ZJM
     */
    deleteRow(row, column) {
      this.$XModal.confirm(MSG.CONFIRM_DELETE).then(type => {
        if (type === 'confirm') {
          this.$refs.xGrid.remove(row);
        }
      });
    },
    /**
     * @description: 执行新增一行
     * @param row 行数据
     * @return: 无
     * @author: ZJM
     */
    async insertEvent() {
      const record = {
        sex: 1,
        companyId: 8,
        role: []
      };
      for (var item in this.extendPropertyValueVoList) {
        // if(this.extendPropertyValueVoList[item].propertyType===1){
        record[this.extendPropertyValueVoList[item].propertyCode] = '';
        // } else
      }
      // 往表格插入临时数据，从指定位置插入一行或多行
      await this.$refs.xGrid.insertAt(record, -1);
    },
    async copyRow(row, column) {
      this.$refs.xGrid.clearActived();
      // const index = this.$refs.xGrid.$getRowIndex(row);
      const record = { ...row };
      delete record._XID;
      // 往表格插入临时数据，从指定位置插入一行或多行
      await this.$refs.xGrid.insertAt(record, row);
    },
    /**
     * @description: 表单提交
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    async submit() {
      try {
        const {
          // 新增的数据
          insertRecords
        } = this.$refs.xGrid.getRecordset();

        const data = JSON.parse(JSON.stringify(insertRecords));
        const res = await userInsertBatchAction(
          {},
          data);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.setUserReleRole(res.rows, insertRecords);
          setTimeout(() => {
            if (this.extendPropertyValueVoList.length > 0) {
              this.saveExtend(res.rows, insertRecords);
            } else {
              this.goBack();
            }
          }, 0);
          // 提示正常信息
          showSuccessMessage(res.message);
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    async setUserReleRole(list, records) {
      var arr = [];
      for (var item in records) {
        if (records[item].role.length > 0) {
          for (var i = 0; i < records[item].role.length; i++) {
            var json = {};
            json.userId = list[item].userId;
            json.roleId = records[item].role[i];
            arr.push(json);
          }
        }
      }
      if (arr.length > 0) {
        try {
          const res = await userRoleRelationshipInsertOrUpdateBatchAction({}, arr);
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          } else {
            // 提示异常信息
            showWarningMessage(res.message);
          }
        } catch (error) {
          showErrorNotify(TITLE.NOTIFY, error);
        } finally {
        }
      }
    },
    /**
     * @description: 执行验证
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    async fullValidEvent() {
      const errMap = await this.$refs.xGrid
        .fullValidate()
        .catch(errMap => errMap);
      if (errMap) {
        var msgList = [];
        Object.values(errMap).forEach(errList => {
          errList.forEach((params, index) => {
            const { column, rules } = params;
            rules.forEach(rule => {
              msgList.push(
                `${VARIABLE.THE} ${index + 1} ${VARIABLE.ROW} ${column.title} ${MSG.VALIDATION_FAILED}：${rule.message}`
              );
            });
          });
        });
        this.$XModal.alert({
          status: 'error',
          message: () => {
            return [
              <div class="red" style="max-height: 400px;overflow: auto;">
                {msgList.map(msg => (
                  <div>{msg}</div>
                ))}
              </div>
            ];
          }
        });
      } else {
        // 通过验证，执行提交
        this.submit();
      }
    },
    /**
     * @description: 保存表头扩展字段数据
     * @return: 无
     * @author: ZJM
     */
    saveExtend(row, record) {
      var arr = [];
      for (var x in row) {
        for (var y in this.extendPropertyValueVoList) {
          var json = {
            extendId: this.extendPropertyValueVoList[y].extendId,
            extendValue: record[x][this.extendPropertyValueVoList[y].propertyCode],
            propertyCode: this.extendPropertyValueVoList[y].propertyCode,
            relationshipTypeId: row[x].userId
          };
          arr.push(json);
        }
      }
      this.setSaveExtend(arr);
    },
    /**
     * @description: 执行扩展字段保存接口
     * @return: 无
     * @author: ZJM
     */
    async setSaveExtend(arr) {
      try {
        const res = await extendPropertyValueInsertOrUpdateBatchAction({}, arr);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.goBack();
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 执行扩展字段验证遍历
     * @return: 无
     * @author: ZJM
     */
    extendRules(row) {
      this.validRules = this.OldRules;
      for (var item in this.extendPropertyValueVoList) {
        if (this.extendPropertyValueVoList[item].propertyIsNull === 1) {
          // 若类型是日期
          if (this.extendPropertyValueVoList[item].propertyType === 3) {
            this.validRules[this.extendPropertyValueVoList[item].propertyCode] = [
              { required: true, message: MSG.PLEASE_ENTER_CONTENT },
              { validator: this.setValidateForm }
            ];
          } else { // 若类型是字符或数字
            this.validRules[this.extendPropertyValueVoList[item].propertyCode] = [
              { required: true, message: MSG.PLEASE_ENTER_CONTENT },
              { validator: this.setValidateForm }
            ];
          }
        } else {
          // 若类型是日期
          if (this.extendPropertyValueVoList[item].propertyType === 3) {
            this.validRules[this.extendPropertyValueVoList[item].propertyCode] = [
              { validator: this.setValidateForm }
            ];
          } else { // 若类型是字符或数字
            this.validRules[this.extendPropertyValueVoList[item].propertyCode] = [
              { validator: this.setValidateForm }
            ];
          }
        }
      }
    },
    /**
     * @description: 返回按钮点击
     * @param 无
     * @return:无
     * @author: ZJM
     */
    goBack() {
      this.$emit('goBack', VARIABLE.ZERO_NUMBER);
    },
    async toSearchRole() {
      try {
        const res = await roleGetAllAction();
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.tableColumns[8].editRender.options = res.rows.map((item) => {
            item.value = item.roleId;
            item.label = item.roleName;
            return item;
          });
        } else {
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(this.GLOBAL.TITLE.NOTIFY, error);
      } finally {
      }
    }
  }
};
